<div class="outcomes-view"><template v-if="outcome.reactionTime || outcome.conversion"><div class="title">Details</div><div class="details"><template v-if="outcome.reactionTime"><div class="label">Reaction Time</div><div class="value">{{reactionTime}}</div></template><template v-if="outcome.conversion"><div class="label">Conversion</div><div class="value">{{outcome.conversion.value}} {{isNaN(outcome.conversion.precision) ? "" : ` ${outcome.conversion.precision}`}}</div></template></div></template><div class="title">Products</div><div class="sub-section"><div class="tabs"><div class="tab" v-for="(product, idx) in outcome.productsList" @click="productsIdx = idx" :class="productsIdx === idx ? &quot;selected&quot; : &quot;&quot;">Product {{idx + 1}}</div></div><div class="compound"><CompoundView :component="outcome.productsList[productsIdx]"></CompoundView></div><div class="sub-title">Measurements</div><div class="measurements"><div class="label">Type</div><div class="label">Value</div><div class="label">Analysis</div><div class="label">Raw</div><template v-for="measurement in outcome.productsList[productsIdx].measurementsList"><div class="value">{{getMeasurementType(measurement.type)}}</div><div class="value">{{getMeasurementValue(measurement)}}</div><div class="value">{{measurement.analysisKey}}</div><div class="value"> <div class="raw"> <div class="button" @click="setRawMeasurement(measurement)">&lt;></div></div></div></template></div><floating-modal v-if="showRawMeasurement" title="Raw Data" @closeModal="showRawMeasurement=false"><div class="data"><pre>{{rawMeasurement}}</pre></div></floating-modal></div><template v-if="outcome.analysesMap?.length"><div class="title">Analyses</div><div class="sub-section"><div class="tabs"><div class="tab" v-for="(analysis, idx) in outcome.analysesMap" @click="analysesIdx = idx" :class="analysesIdx === idx ? &quot;selected&quot; : &quot;&quot;">{{analysis[0]}}</div></div><div class="details"><div class="label">Type</div><div class="value">{{getAnalysisType(outcome.analysesMap[analysesIdx][1].type)}}</div><div class="label">Details</div><div class="value">{{outcome.analysesMap[analysesIdx][1].details}}</div><div class="label">Raw</div><div class="value"><div class="raw"><div class="button" @click="setRawAnalysis(outcome.analysesMap[analysesIdx][1])">&lt;></div></div></div></div></div><floating-modal v-if="showRawAnalysis" title="Raw Data" @closeModal="showRawAnalysis=false"><div class="data"><pre>{{rawAnalysis}}</pre></div></floating-modal></template></div>