<div class="workups-view"><div class="details"><div class="label">Type</div><div class="value">{{workupType}}</div><template v-if="workup.details"><div class="label">Details</div><div class="value">{{workup.details}}</div></template><template v-if="workup.duration"><div class="label">Duration</div><div class="value">{{workup.duration}}</div></template><template v-if="workup.amount"><div class="label">Aliquot amount</div><div class="value">{{workup.amount}}</div></template><template v-if="workup.keepPhase"><div class="label">Phase kept</div><div class="value">{{workup.keepPhase}}</div></template><template v-if="workup.targetPh"><div class="label">Target pH</div><div class="value">{{workup.targetPh}}</div></template><template v-if="workup.isAutomated"><div class="label">Automated</div><div class="value">{{workup.Automated}}</div></template></div><div class="inputs" v-if="workup.input"><div class="title">Inputs</div><div class="components"><template v-for="component in workup.input.componentsList"><div class="identifier">{{getIdentifier(component.identifiersList)}}</div><div class="amount">{{getAmount(component.amount)}}</div></template></div></div></div>