<div class="main-reaction-view"><transition name="fade"><div class="loading" v-if="loading"><LoadingSpinner></LoadingSpinner></div></transition><transition name="fade"><div class="error-message" v-if="traceback"> <code> <pre>{{ traceback }}</pre></code></div><div class="reaction-transition" v-else-if="!loading"><div class="nav-holder"><div class="nav"><div class="nav-item" v-for="item in navItems" :class="activeNav == item ? &quot;active&quot; : &quot;&quot;" @click="scrollTo(item)">{{item.replaceAll("-"," ")}}</div></div></div><div class="content"><div class="title">Summary</div><div class="section" id="summary"><div class="summary" v-if="reactionSummary"><div class="display" v-html="reactionSummary"></div></div></div><div id="identifiers" v-if="reaction?.identifiersList?.length"><div class="title">Identifiers</div><div class="section"><div class="identifiers"><template v-for="identifier in reaction.identifiersList"><div class="value">{{getReactionType(identifier.type)}}</div><div class="value">{{identifier.value}}</div><div class="value">{{identifier.details}}</div></template></div></div></div><div id="inputs" v-if="reaction?.inputsMap?.length"><div class="title">Inputs</div><div class="section"><div class="tabs"><div class="tab" v-for="(input, idx) in reaction.inputsMap" @click="inputsIdx = idx" :class="inputsIdx == idx ? &quot;selected&quot; : &quot;&quot;">{{input[0]}}</div></div><div class="input"><div class="title">Details</div><div class="details"><template v-for="key in Object.keys(displayDetails)"><div class="label">{{key.replaceAll(/(?=[A-Z])/g, ' ')}}</div><div class="value">{{displayDetails[key]}}</div></template></div><div class="title">Components</div><div class="compound"><template v-for="component in reaction.inputsMap[inputsIdx][1].componentsList"><CompoundView :component="component"></CompoundView></template></div></div></div></div><div id="setup" v-if="reaction?.setup"><div class="title">Setup</div><div class="section"><div class="tabs"><template v-for="tab in setupTabs"><div class="tab capitalize" @click="setupTab = tab" :class="setupTab === tab ? &quot;selected&quot; : &quot;&quot;" v-if="tab !== &quot;automation&quot; || reaction.setup.is_automated">{{tab}}</div></template></div><div class="details"><SetupView :setup="reaction.setup" :display="setupTab"></SetupView></div></div></div><div id="conditions" v-if="reaction?.conditions"><div class="title">Conditions</div><div class="section"><div class="tabs"><template v-for="tab in conditionTabs"><div class="tab capitalize" @click="conditionTab = tab" :class="conditionTab === tab ? &quot;selected&quot; : &quot;&quot;" v-if="reaction.conditions[tab] || (tab === &quot;other&quot; &amp;&amp; displayConditionsOther)">{{tab}}</div></template></div><div class="details"><ConditionsView :conditions="reaction.conditions" :display="conditionTab"></ConditionsView></div></div></div><div id="notes" v-if="reaction.notes"><div class="title">Notes</div><div class="section"><div class="details"><NotesView :notes="reaction.notes"></NotesView></div></div></div><div id="observations" v-if="reaction.observationsList?.length"><div class="title">Observations</div><div class="section"><div class="details"><ObservationsView :observations="reaction.observationsList"></ObservationsView></div></div></div><!-- TODO flesh out workups section--><div id="workups" v-if="reaction.workupsList?.length"><div class="title">Workups</div><div class="section"><div class="tabs"><div class="tab capitalize" v-for="(workup, idx) in reaction.workupsList" @click="workupsTab = idx" :class="workupsTab === idx ? &quot;selected&quot; : &quot;&quot;">{{getWorkupLabel(workup.type)}}</div></div><div class="details"><WorkupsView :workup="reaction.workupsList[workupsTab]"></WorkupsView></div></div></div><div id="outcomes" v-if="reaction.outcomesList?.length"><div class="title">Outcomes</div><div class="section"><div class="tabs"><div class="tab capitalize" v-for="(outcome, idx) in reaction.outcomesList" @click="outcomesTab = idx" :class="outcomesTab === idx ? &quot;selected&quot; : &quot;&quot;">Outcome {{idx + 1}}</div></div><div class="details"><OutcomesView :outcome="reaction.outcomesList[outcomesTab]"></OutcomesView></div></div></div><div id="provenance" v-if="reaction.provenance"><div class="title">Provenance</div><div class="section"><ProvenanceView :provenance="reaction.provenance"></ProvenanceView></div></div><div id="events" v-if="events?.length"><div class="title">Record Events</div><div class="section"><EventsView :events="events"></EventsView></div></div><div id="full-record" v-if="reaction"><div class="title">Full Record</div><div class="section"><div class="full-record button" @click="showRawReaction=true">View Full Record</div></div><FloatingModal v-if="showRawReaction" title="Raw Data" @closeModal="showRawReaction=false"><div class="data"><pre>{{reaction}}</pre></div></FloatingModal></div></div></div></transition></div>