<div class="setup-view"><div class="vessel details" v-if="display==&quot;vessel&quot;"><div class="label">Type</div><div class="value">{{vesselType}}</div><template v-if="vessel.details"><div class="label">Details</div><div class="value">{{vessel.details}}</div></template><div class="label">Material</div><div class="value">{{vesselMaterial || "UNSPECIFIED"}}</div><div class="label">Volume</div><div class="value">{{vesselVolume}}</div><template v-if="vessel.attachmentsList?.length"><div class="label">Attachments</div><div class="value">{{vesselAttachments}}</div></template><template v-if="vessel.preparationsList?.length"><div class="label">Preparations</div><div class="value">{{vesselPrep}}</div></template></div><div class="environment details" v-if="display==&quot;environment&quot;"><div class="label">Type</div><div class="value">{{reactionEnv || "UNSPECIFIED"}}</div><template v-if="setup.environment?.details"><div class="label">Details</div><div class="value">{{setup.environment.details}}</div></template></div><div class="automated details" v-if="display==&quot;automated&quot;"><div class="label">Platform</div><div class="value">{{setup.automation_platform || "UNSPECIFIED"}}</div></div></div>