<div class="compound-view" :style="{gridTemplateColumns: gridColumns}"><div class="label">Compound</div><div class="label" v-if="component.amount">Amount</div><div class="label">Role</div><div class="label">Raw</div><div class="svg" v-html="compoundSVG"></div><div class="amount" v-if="component.amount">{{compoundAmount}}</div><div class="role">{{compoundRole.toLowerCase()}}</div><div class="raw"> <div class="button" @click="showRawData=true">&lt;> </div></div><floating-modal v-if="showRawData" title="Raw Data" @closeModal="showRawData=false"><div class="data"><pre v-for="identifier in rawData.identifiers">identifiers: {{identifier}}</pre><pre v-if="rawData.amount">amount: {{rawData.amount}}</pre><pre>reaction_role: {{rawData.reaction_role}}</pre><pre v-if="rawData.is_desired_product">is_desired_product: {{rawData.is_desired_product}}</pre><pre v-if="rawData.isolated_color">isolated_color: {{rawData.isolated_color}}</pre><pre v-if="rawData.texture">texture: {{rawData.texture}}</pre><pre v-for="prep in rawData.preparations">preparations: {{prep}}</pre></div></floating-modal></div>