<div class="provenance-view"><template v-if="provenance.experimenter"><div class="title">Experimenter</div><div class="details experimenter"><div class="label">Username</div><div class="label">Name</div><div class="label">ORCID</div><div class="label">Organization</div><div class="label">Email</div><div class="value">{{provenance.experimenter?.username}}</div><div class="value">{{provenance.experimenter?.name}}</div><div class="value">{{provenance.experimenter?.orcid}}</div><div class="value">{{provenance.experimenter?.organization}}</div><div class="value">{{provenance.experimenter?.email}}</div></div></template><div class="details"><template v-if="provenance.city"><div class="label">City</div><div class="value">{{provenance.city}}</div></template><template v-if="provenance.experimentStart"><div class="label">City</div><div class="value">{{new Date(provenance.experimentStart).toLocaleDateString()}}</div></template><template v-if="provenance.doi"><div class="label">DOI</div><div class="value">{{provenance.doi}}</div></template><template v-if="provenance.patent"><div class="label">Patent</div><div class="value">{{provenance.patent}}</div></template><template v-if="provenance.publicationUrl"><div class="label">Publication URL</div><div class="value"> <a :href="provenance.publicationUrl" target="_blank">{{provenance.publicationUrl}}</a></div></template></div></div>