<div class="conditions-view"><div class="temperature details" v-if="display===&quot;temperature&quot;"><div class="label">Control Type</div><div class="value">{{tempType}}</div><template v-if="conditions.temperature?.control.details"><div class="label">Details</div><div class="value">{{conditions.temperature?.control.details}}</div></template><div class="label">Setpoint</div><div class="value">{{tempSetPoint}}</div><!-- TODO Flesh out temp measurements--><template v-if="conditions.temperature?.measurementsList?.length"><div class="label">Measurements</div><div class="value">{{conditions.temperature.measurementsList}}</div></template></div><div class="pressure details" v-if="display===&quot;pressure&quot;"><div class="label">Control Type</div><div class="value">{{pressureType}}</div><template v-if="conditions.pressure?.control.details"><div class="label">Details</div><div class="value">{{conditions.pressure?.control.details}}</div></template><div class="label">Setpoint</div><div class="value">{{pressureSetPoint}}</div><div class="label">Atmosphere</div><div class="value">{{pressureAtmo}}</div><!-- TODO Flesh out pressure measurements--><template v-if="conditions.pressure.measurementsList?.length"><div class="label">Measurements</div><div class="value">{{conditions.pressure.measurementsList}}</div></template></div><div class="stirring details" v-if="display===&quot;stirring&quot;"><div class="label">Type</div><div class="value">{{stirType}}</div><template v-if="conditions.stirring?.details"><div class="label">Details</div><div class="value">{{conditions.stirring?.details}}</div></template><div class="label">Rate</div><div class="value">{{stirRate || "UNSPECIFIED"}}</div><template v-if="conditions.stirring?.rate?.rpm"><div class="label">RPM</div><div class="value">{{conditions.stirring?.rate?.rpm}}</div></template></div><div class="illumination details" v-if="display===&quot;illumination&quot;"><div class="label">Type</div><div class="value">{{illumType}}</div><!-- TODO flesh out wave length--><div class="label">Peak Wavelength</div><div class="value">{{conditions.illumination?.peakWaveLength || "None"}}</div><template v-if="conditions.illumination?.color"><div class="label">Color</div><div class="value">{{conditions.illumination?.color}}</div></template><!-- TODO flesh out distance--><div class="label">Distance to Vessel</div><div class="value">{{conditions.illumination?.distanceToVessel || "None"}}</div></div><!-- TODO flesh out electrochemistry--><div class="electro details" v-if="display === &quot;electrochemistry&quot;"><div class="label">Type</div><div class="value">{{conditions.electrochemistry}}</div></div><!-- TODO flesh out flow--><div class="electro details" v-if="display === &quot;flow&quot;"><div class="label">Type</div><div class="value">{{conditions.flow}}</div></div><!-- TODO flesh out other--><div class="other details" v-if="display === &quot;other&quot;"><template v-if="conditions.reflux"><div class="label">Reflux</div><div class="value">{{conditions.reflux}}</div></template><template v-if="conditions.ph"><div class="label">pH</div><div class="value">{{conditions.ph}}</div></template><template v-if="conditions.conditions_are_dynamic"><div class="label">Conditions are dynamic</div><div class="value">{{conditions.conditions_are_dynamic}}</div></template><template v-if="conditions.details"><div class="label">Details</div><div class="value">{{conditions.details}}</div></template></div></div>