<div class="events-view"><div class="tabs"><div class="tab" v-for="(event, idx) in events" @click="eventIdx = idx" :class="eventIdx === idx ? &quot;selected&quot; : &quot;&quot;">{{new Date(event.time?.value).toLocaleString()}}</div></div><div class="details"><template v-if="events[eventIdx].details"><div class="label">Details</div><div class="value">{{events[eventIdx].details}}</div></template><template v-if="events[eventIdx].person?.username"><div class="label">Username</div><div class="value">{{events[eventIdx].person.username}}</div></template><template v-if="events[eventIdx].person?.name"><div class="label">Name</div><div class="value">{{events[eventIdx].person.name}}</div></template><template v-if="events[eventIdx].person?.orcid"><div class="label">ORCID</div><div class="value">{{events[eventIdx].person.orcid}}</div></template><template v-if="events[eventIdx].person?.organization"><div class="label">Organization</div><div class="value">{{events[eventIdx].person.organization}}</div></template><template v-if="events[eventIdx].person?.email"><div class="label">Email</div><div class="value">{{events[eventIdx].person.email}}</div></template></div></div>